import React from 'react';
import css from './style.less';
import {Col, Container, FormControl, InputGroup, Row} from 'react-bootstrap';
import Header from '../component/Header';

class Home extends React.Component {
  state = {
    originalResume: 'paste here!',
  };

  renderInput = () => (
    <Col className={css.example}>
      <label className={css.label}>Paste your resume!</label><br />
      <FormControl
        className={css.resumeInput}
        as="textarea"
        rows="10"
        onChange={(e) => setState({originalResume: e.target.value})}
        value={this.state.originalResume}
      />
    </Col>
  );

  renderResult = () => (
    <Col>
      {this.state.originalResume}
    </Col>
  );

  render() {
    return (
      <div>
        <Header />
        <Container className="mt-5">
          <Row>
            {this.renderInput()}
            {this.renderResult()}
          </Row>
        </Container>
      </div>
    );
  }
}

export default Home;
